.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: "Crimson Text", serif;
  -ms-flex-align: center;
      align-items: center;
	color: #2A2B2E;
}

.App > footer {
  height: 80vh;
}

@media only screen and (max-width: 1100px) {
  html,
  body,
  .App {
    overflow-x: hidden;
    height: auto;
  }

  .App > footer {
    height: auto;
  }
}

@media only screen and (max-height: 800px) and (min-width: 1100px) {
  .App > footer {
    height: 640px;
  }
}



